.TaskReport .ant-input {
  border-radius: 0;
}

.ReportDesign .ant-input {
  border-radius: 0;
}

.ReportDesign .ant-btn {
  border-radius: 0;
  height: 32px;
}

.ReportDesign .ant-select-selection {
  border-radius: 0;
}

.ReportDesign .ant-collapse {
  border-radius: 0;
  background: white;
  border: 1px solid #ddd;
}

.ReportDesign .ant-collapse .ant-collapse-item {
  border-bottom: 0;
}

.ReportDesign .ant-collapse .ant-collapse-header {
  border-radius: 0;
  background: #f5f5f6;
  border: 1px solid #ddd;
  font-size: 14px;
  color: #2a2f32;
}

.ReportDesign .ant-collapse-item .operationGroup {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: #f5f5f6;
}
.ReportDesign .ant-col-10,.ReportDesign .ant-col-16{
  background-color: #f5f5f5;
}
.ReportDesign .ant-collapse-item .ant-collapse-content {
  padding: 0;
  border: none;
  border-top: 0;
  border-radius: 0!important;
  width:90%;
  margin-left:5%;
}

.ReportDesign .insideCollaspse .ant-collapse-header {
  border-left: 0;
  border-right: 0;
}

.ReportDesign .insideCollaspse .ant-collapse-content {
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}


/*.ReportDesign .Panel-Level1 .ant-collapse > .ant-collapse-content > .ant-collapse-content-box > .ant-row {
  padding: 0 36px;
}*/


/*level1*/

.ReportDesign .Panel-Level1 {
  margin: 4px 0;
}

.ReportDesign .Panel-Level1>.ant-collapse-header {
  padding-left: 40px!important;
}

.ReportDesign .Panel-Level1>.ant-collapse-header>.arrow {
  margin-left: 8px;
}

.ReportDesign .Panel-Level1>div.ant-collapse-content.ant-collapse-content-active>div>.ant-row:nth-child(1) {
  padding: 0 36px;
  font-size: 12px;
  color: #999;
  margin-bottom: 12px;
}

.ReportDesign .Panel-Level1>div.ant-collapse-content.ant-collapse-content-active>div>.ant-row:nth-child(3) {
  padding: 12px 36px 0;
}


